body {
    font-family: Arial;
}

.game {
    position: relative;
}

.playerNow {
    position: absolute;
    bottom: .5em;
    font-size: 35px;
}

.board {
    display: flex;
    flex-direction: column;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.count {
    font-size: 20px;
    position: absolute;
    top: 0;
    left: 25%;
}

.row:first-child {
    margin-left: -4px;
}

.row:not(:first-child) {
    margin-top: -4px;
}

.row:last-child {
    margin-top: -4px;
    margin-left: -4px;
}

.row:not(:first-child) > .square:first-child {
    margin-right: -4px;
}

.row:last-child > .square:first-child {
    margin-right: 0;
}

.square {
    position: relative;
    width: 40px;
    height: 40px;
}

.square > * {
    position: absolute;
    background-color: lightgrey;
    z-index: 1;
}

.square > div[state='false']:hover {
    background-color: grey;
    z-index: 2;
}

.square > .up,
.square > .down {
    width: 40px;
    height: 4px;
}

.square > .right,
.square > .left {
    width: 4px;
    height: 40px;
}

.square > .up {
    top: 0;
}

.square > .right {
    right: 0;
}

.square > .down {
    bottom: 0;
}

.square > .left {
    left: 0;
}

.square > *[state='true'] {
    z-index: 2;
    background-color: black;
}

.square > span {
    font-size: 30px;
    background-color: transparent;
    left: 50%;
    top: 50%;
    margin-left: -12px;
    margin-top: -16px;
    cursor: default;
}
